import React, {Component} from 'react'
import {
  NavBar,
  Icon,
  InputItem,
  List,
  Button,
  TextareaItem,
  WingBlank,
  Toast
} from 'antd-mobile'
import './index'
import {api} from "../../config/index"
import {connect} from 'react-redux'

@connect(state => state, {})
export default class AddJob extends Component {

  constructor(props) {
    super(props);
    this.state = {
      form: {
        title: 'Node',
        salary: '20-30K',
        education: '',
        experience: '3',
        address: '-',
        details: `1. \n2. \n3. \n4. `
      }
    };
    this.onChange = this.onChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  async handleSubmit() {
    const {form} = this.state;
    const {user, history} = this.props;
    form.authorId = user._id;
    try {
      let ret = await api.addJob(form);
      Toast.success('');
      history.goBack();
    } catch (e) {
      console.log(e);
    }
  }

  onChange(key, value) {
    const {form} = this.state;
    form[key] = value;
    this.setState({form});
  }

  render() {
    const {history} = this.props;
    const {form} = this.state;
    return (
      <div className="page-addJob">
        <div className="mi-header">
          <NavBar icon={<Icon type="left"/>} onLeftClick={() => history.goBack()}></NavBar>
        </div>
        <div className="mi-content">
          <List>
            <InputItem
              type="text"
              placeholder=""
              onChange={(v) => this.onChange('title', v)}
              value={form.title}
            ></InputItem>
            <InputItem
              type="text"
              placeholder=""
              onChange={(v) => this.onChange('salary', v)}
              value={form.salary}
            ></InputItem>
            <InputItem
              type="text"
              placeholder=""
              onChange={(v) => this.onChange('education', v)}
              value={form.education}
            ></InputItem>
            <InputItem
              type="text"
              placeholder=""
              onChange={(v) => this.onChange('experience', v)}
              value={form.experience}
            ></InputItem>
            <InputItem
              type="text"
              placeholder=""
              onChange={(v) => this.onChange('address', v)}
              value={form.address}
            ></InputItem>
            <TextareaItem
              type="text"
              title=""
              autoHeight
              onChange={(v) => this.onChange('details', v)}
              value={form.details}
            />
          </List>
          <WingBlank>
            <Button
              className="btn-submit"
              type="primary"
              onClick={this.handleSubmit}
            ></Button>
          </WingBlank>
        </div>
      </div>
    )
  }
}


